import React from 'react';
import { FaExclamationTriangle } from 'react-icons/fa'; // Import the icon


const ScreenWarning = () => {
  const [windowSize, setWindowSize] = React.useState({
    width: undefined,
    height: undefined,
  });

  React.useEffect(() => {
    // Handler to call on window resize
    function handleResize() {
      // Set window width/height to state
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    }
    
    // Add event listener
    window.addEventListener("resize", handleResize);
    
    // Call handler right away so state gets updated with initial window size
    handleResize();
    
    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleResize);
  }, []); // Empty array ensures that effect is only run on mount and unmount

  return (
    <div>
      {windowSize.width < 900 && ( // Your size check here
        <div className="screen-warning">
            <div>
            <FaExclamationTriangle size={32} /> {/* Icon with size specified */}
            </div>
            <div>
          <p>For the best experience, please expand your browser window or use a device with a larger screen.</p>
          </div>
    </div>
      )}
    </div>
  );
};

export default ScreenWarning;
